    "<p>You have not installed an Info Adapter. The adapter shows you information about the system and is required to display important messages from the ioBroker team.</p><p>Do you want to install the info adapter?</p>": {"en": "<p>You have not installed an Info Adapter. The adapter shows you information about the system and is required to display important messages from the ioBroker team.</p><p>Do you want to install the info adapter?</p>", "de": "<p>Sie haben keinen Info Adapter installiert. Der Adapter zeigt Ihnen Informationen zum System und ist erforderlich, um wichtige Meldungen des ioBroker-Teams anzuzeigen.</p><p>Möchten Sie den Info-Adapter installieren?</p>", "ru": "<p>Вы не установили информационный адаптер. Адаптер показывает вам информацию о системе и необходим для отображения важных сообщений от команды ioBroker.</p><p>Хотите установить информационный адаптер?</p>", "pt": "<p>Você não instalou um adaptador de informações. O adaptador mostra informações sobre o sistema e é necessário para exibir mensagens importantes da equipe do ioBroker.</p><p>Deseja instalar o adaptador de informações?</p>", "nl": "<p>U hebt geen Info-adapter geïnstalleerd. De adapter geeft informatie over het systeem weer en is nodig om belangrijke berichten van het ioBroker-team weer te geven.</p><p>Wilt u de infolader installeren?</p>", "fr": "<p>Vous n’avez pas installé d’Info Adapter. L’adaptateur affiche des informations sur le système et est nécessaire pour afficher les messages importants de l’équipe ioBroker.</p><p>Souhaitez-vous installer l’adaptateur d’informations?</p>", "it": "<p>Non hai installato un adattatore informazioni. L'adattatore mostra le informazioni sul sistema ed è necessario per visualizzare messaggi importanti dal team ioBroker.</p><p>Vuoi installare l'adattatore di informazioni?</p>", "es": "<p>No ha instalado un adaptador de información. El adaptador le muestra información sobre el sistema y debe mostrar mensajes importantes del equipo de ioBroker.</p><p>¿Desea instalar el adaptador de información?</p>", "pl": "<p>Nie masz zainstalowanego adaptera Info. Adapter wyświetla informacje o systemie i jest wymagany do wyświetlania ważnych wiadomości od twórców ioBroker.</p><p>Czy chcesz zainstalować adapter Info?</p>", "zh-cn": "<p>您尚未安装信息插件。该插件显示有关系统的信息，并且需要显示来自ioBroker团队的重要消息。</p><p>是否要安装信息插件？</p>"},
    "<p>Your version of the info adapter is outdated. It is strongly recommended to update this to get on the enjoyment of the innovations. Only with the new version, for example, messages from the ioBroker team can be displayed directly.</p><p>Would you like to update the info adapter?</p>": {"en": "<p>Your version of the info adapter is outdated. It is strongly recommended to update this to get on the enjoyment of the innovations. Only with the new version, for example, messages from the ioBroker team can be displayed directly.</p><p>Would you like to update the info adapter?</p>", "de": "<p>Ihre Version des Info-Adapters ist veraltet. Es wird dringend empfohlen, dies zu aktualisieren, um die Neuerungen genießen zu können. Nur mit der neuen Version können beispielsweise Meldungen des ioBroker-Teams direkt angezeigt werden.</p><p>Möchten Sie den Info-Adapter aktualisieren?</p>", "ru": "<p>Ваша версия информационного адаптера устарела. Настоятельно рекомендуется обновить это, чтобы получить удовольствие от инноваций. Например, только в новой версии сообщения от команды ioBroker могут отображаться напрямую.</p><p>Хотите обновить информационный адаптер?</p>", "pt": "<p>Sua versão do adaptador de informações está desatualizada. É altamente recomendável atualizar isso para aproveitar o prazer das inovações. Somente com a nova versão, por exemplo, mensagens da equipe do ioBroker podem ser exibidas diretamente. </ P> <p> Você gostaria de atualizar o adaptador de informações?</p>", "nl": "<p>Uw versie van de infolader is verouderd. Het wordt ten zeerste aanbevolen om dit bij te werken om te genieten van de innovaties. Alleen met de nieuwe versie kunnen berichten van het ioBroker-team bijvoorbeeld direct worden weergegeven.</p><p>Wilt u de infolader bijwerken?</p>", "fr": "<p>Votre version de l’adaptateur d’informations est obsolète. Il est fortement recommandé de le mettre à jour pour profiter pleinement des innovations. Par exemple, avec la nouvelle version uniquement, les messages de l’équipe ioBroker peuvent être affichés directement.</p><p>Souhaitez-vous mettre à jour l’adaptateur d’informations?</p>", "it": "<p>La tua versione dell'adattatore informazioni non è aggiornata. Si consiglia vivamente d'aggiornarla, per ottenere sempre le novità. Solo con la nuova versione, ad esempio, i messaggi del team ioBroker possono essere visualizzati direttamente.</p><p>Vuoi aggiornare l'adattatore informazioni?</p>", "es": "<p>Su versión del adaptador de información no está actualizada. Se recomienda encarecidamente actualizar esto para disfrutar de las innovaciones. Solo con la nueva versión, por ejemplo, los mensajes del equipo ioBroker se pueden mostrar directamente.</p><p>¿Desea actualizar el adaptador de información?</p>", "pl": "<p>Twoja wersja adaptera Info jest nieaktualna. Zalecana jest aktualizacja, aby korzystać z nowych funkcji. Tylko dzięki nowej wersji możliwe jest np. wyświetlanie bezpośrednio wiadomości od twórców ioBroker.</p><p>Czy chcesz zaktualizować adapter Info?</p>", "zh-cn": "<p>您的信息插件版本已过时。强烈建议更新此内容以获得创新的乐趣。例如，只有使用新版本，才能直接显示来自ioBroker团队的消息。</p><p>是否要更新信息插件？</p>"},
