    "This version of node.js \"%s\" on \"%s\" is deprecated. Please install node.js 6, 8 or newer": {"en": "ioBroker supports the LTS versions 6, 8, 10 or newer of node.js. Please update your version (\"%s\") on host \"%s\" to one of the supported versions. We recommend to use Node.js 6.", "de": "ioBroker unterstützt die LTS Versionen 10, 12 oder neuer von Node.js. Bitte aktualisieren Sie die verwendete Node.js Version (\"%s\") auf dem Rechner \"%s\" auf eine unterstützte Version. Wir empfehlen die Verwendung von Node.js 12.", "ru": "Эта версия node.js \"%s\" на \"%s\" устарела. Установите node.js 6, 8, 10 или новее.", "pt": "ioBroker suporta as versões LTS 6, 8 e 10 do Node.js. Atualize sua versão (\"%s\") no host \"%s\" para uma das versões suportadas. Recomendamos usar Node.js 6.", "nl": "ioBroker ondersteunt de LTS-versies 6, 8 en 10 van node.js. Update uw versie (\"%s\") op host \"%s\" naar een van de ondersteunde versies. We raden aan om node.js 6 te gebruiken.", "fr": "ioBroker supporte les versions LTS 6, 8 et 10 de node.js. Veuillez mettre à jour votre version (\"%s\") sur l'hôte \"%s\" avec une des versions prises en charge. Nous vous recommandons d'utiliser node.js 6.", "it": "ioBroker supporta le versioni LTS 6, 8 e 10 di node.js. Aggiorna la tua versione (\"%s\") sull'host \"%s\" a una delle versioni supportate. Si consiglia di utilizzare node.js 6.", "es": "ioBroker soporta las versiones LTS 6, 8 y 10 de Node.js. Actualice su versión (\"%s\") en el host \"%s\" para una de las versiones soportadas. Recomendamos usar Node.js 6.", "pl": "ioBroker obsługuje wersje LTS 6, 8 i 10 pliku node.js. Zaktualizuj swoją wersję (\" %s\") na hoście \" %s\" do jednej z obsługiwanych wersji. Zalecamy korzystanie z Node.js 6.", "zh-cn": "ioBroker支持LTS版本6,8,10,12或更新的node.js.请将主机\"%s\"上的版本（\"%s\"）更新为其中一个受支持的版本。我们建议使用Node.js 10。"},
