    "Warning1": {                                    "en": "Don't install adapters from GitHub unless asked to by a developer or if you are 100 %sure what you are doing!", "de": "Installiere keine Adapter von GitHub, außer Du wurdest von einem Entwickler darum gebeten oder Du weißt genau was Du hier tust!", "ru": "Не устанавливайте адаптеры из GitHub, если только об этом не попросит разработчик, или если вы на 100% уверены, что делаете!", "pt": "Não instale adaptadores do GitHub, a menos que solicitado por um desenvolvedor ou se você estiver 100% certo do que está fazendo!", "nl": "Installeer geen adapters van GitHub tenzij hierom wordt gevraagd door een ontwikkelaar of als u 100% zeker weet wat u doet!", "fr": "N'installez pas d'adaptateurs à partir de GitHub sauf si un développeur vous le demande ou si vous êtes sûr à 100% de ce que vous faites!", "it": "Non installare adattatori da GitHub se non richiesto da uno sviluppatore o se sei sicuro al 100% di quello che stai facendo!", "es": "¡No instales adaptadores de GitHub a menos que un desarrollador te lo pida o si estás 100 %s eguro de lo que estás haciendo!", "pl": "Nie instaluj adapterów z GitHub, chyba że prosi o to deweloper lub jeśli masz 100% pewności, co robisz!", "zh-cn": "除非开发人员要求，否则不要从GitHub安装适配器，否则您是否100％确信自己在做什么！"},
    "Warning2": {                                    "en": "Adapters on GitHub may not work like they should (they are still under development). Only install them if you are participating in a test!", "de": "Adapter von GitHub funktionieren eventuell nicht richtig (da sie noch in Entwicklung sind). Installiere nur Adapter von GitHub, wenn Du an einem Test neuer Adapter teilnimmst!", "ru": "Адаптеры на GitHub могут работать не так, как должны (они все еще находятся в стадии разработки). Устанавливайте их только если вы участвуете в тесте!", "pt": "Os adaptadores no GitHub podem não funcionar como deveriam (eles ainda estão em desenvolvimento). Instale-os somente se você estiver participando de um teste!", "nl": "Adapters op GitHub werken mogelijk niet zoals ze zouden moeten (ze zijn nog in ontwikkeling). Installeer ze alleen als u deelneemt aan een test!", "fr": "Les adaptateurs sur GitHub peuvent ne pas fonctionner comme ils le devraient (ils sont toujours en cours de développement). Installez-les uniquement si vous participez à un test!", "it": "Gli adattatori su GitHub potrebbero non funzionare come dovrebbero (sono ancora in fase di sviluppo). Installali solo se stai partecipando a un test!", "es": "Los adaptadores en GitHub pueden no funcionar como deberían (todavía están en desarrollo). ¡Solo instálalos si estás participando en una prueba!", "pl": "Adaptery w GitHub mogą nie działać tak, jak powinny (wciąż są w fazie rozwoju). Zainstaluj je tylko, jeśli bierzesz udział w teście!", "zh-cn": "GitHub上的适配器可能无法正常工作（它们仍在开发中）。仅在参加测试时才安装它们！"},
