    "You can drag&drop the devices, channels and states to enums": {"en": "You can drag&drop the devices, channels and states to enums", "de": "Sie können Geräte, Kanäle und Zustände per Drag & Drop in Auflistungen ziehen", "ru": "Вы можете перетаскивать устройства, каналы и состояния к категориям", "pt": "Você pode usar drag & drop para classificar os aparelhos, canais e estados em uma enumeração.", "nl": "U kunt de apparaten, kanalen en toestanden naar enums verslepen", "fr": "Vous pouvez faire glisser et déposer les périphériques, les canaux et les états vers les énumérations", "it": "È possibile trascinare e rilasciare dispositivi, canali e stati in enumerazioni", "es": "Usted puede usar drag & drop para clasificar los aparatos, canales y estados en una clasificación", "pl": "Możesz przeciągać i upuszczać urządzenia, kanały i stany do wyliczenia", "zh-cn": "您可以将设备，通道和状态拖放到分类中"},
    "You can drag&drop users to groups": {           "en": "You can drag&drop users to groups",               "de": "Sie können Benutzer per Drag & Drop in Gruppen verschieben", "ru": "Вы можете перетаскивать пользователей в группы",  "pt": "Você pode usar drag & drop para classificar usuários em grupos", "nl": "U kunt gebruikers naar groepen slepen en neerzetten", "fr": "Vous pouvez faire glisser et déposer les utilisateurs vers les groupes", "it": "È possibile trascinare e rilasciare gli utenti in gruppi", "es": "Puede utilizar drag & drop para clasificar usuarios en grupos", "pl": "Możesz przeciągać i upuszczać użytkowników do grup", "zh-cn": "您可以将用户拖放到组中"},
