    "diag-note": {                                   "en": "Help us to make ioBroker even better! <br> Allow the transmission of anonymous usage statistics and crash reports. The exact scope of the data sent to iobroker.org when updating the adapter list can be selected below and is shown in the overview on the right. The data does not contain any personal information and helps us to set the priorities for the further development of ioBroker in the interest of all users and to be able to correct occurring errors quickly.<br>Much thanks for supporting our hard work by agreeing to the transmission of the statistical data!", "de": "Helfen Sie uns, ioBroker noch besser zu machen!<br>Gestatten Sie die Übermittlung anonymer Nutzungs-Statistiken und Absturzmeldungen. Den genauen Umfang der bei einer Aktualisierung der Adapterliste an iobroker.org gesendeten Daten ist unten wählbar und wird rechts in der Übersicht dargestellt. Die Daten enthalten keine persönlichen Informationen und helfen uns die Prioritäten für die Weiterentwicklung von ioBroker im Interesse aller Nutzer richtig zu setzen und auftretende Fehler schnell beheben zu können.<br>Vielen Dank für die Unterstützung unserer harten Arbeit durch die Zustimmung zur Übermittlung der Statisktikdaten!", "ru": "Помогите нам сделать ioBroker еще лучше! <br> Разрешить передачу анонимной статистики использования и отчетов о сбоях. Точный объем данных, отправляемых на iobroker.org при обновлении списка адаптеров, может быть выбран ниже и показан в обзоре справа. Данные не содержат никакой личной информации и помогают нам устанавливать приоритеты для дальнейшего развития ioBroker в интересах всех пользователей и иметь возможность быстро исправлять возникающие ошибки. <br> Большое спасибо за поддержку нашей тяжелой работы, согласившись на передачу статистических данных!", "pt": "Ajude-nos a tornar o ioBroker ainda melhor! <br> Permitir a transmissão de estatísticas de uso anônimas e relatórios de falhas. O escopo exato dos dados enviados ao iobroker.org ao atualizar a lista de adaptadores pode ser selecionado abaixo e é mostrado na visão geral à direita. Os dados não contêm informações pessoais e nos ajudam a definir as prioridades para o desenvolvimento futuro do ioBroker no interesse de todos os usuários e para poder corrigir rapidamente os erros que ocorrem. <br> Muito obrigado por apoiar nosso trabalho árduo, concordando com a transmissão dos dados estatísticos!", "nl": "Help ons om ioBroker nog beter te maken! <br> Sta de verzending van anonieme gebruiksstatistieken en crashrapporten toe. De exacte omvang van de gegevens die naar iobroker.org worden gestuurd bij het bijwerken van de adapterlijst, kan hieronder worden geselecteerd en wordt weergegeven in het overzicht aan de rechterkant. De gegevens bevatten geen persoonlijke informatie en helpen ons om prioriteiten te stellen voor de verdere ontwikkeling van ioBroker in het belang van alle gebruikers en om snel voorkomende fouten te kunnen corrigeren. <br> Hartelijk dank voor uw steun aan ons harde werk door in te stemmen met de verzending van de statistische gegevens!", "fr": "Aidez-nous à rendre ioBroker encore meilleur! <br> Autorisez la transmission de statistiques d'utilisation anonymes et de rapports de plantage. L'étendue exacte des données envoyées à iobroker.org lors de la mise à jour de la liste des adaptateurs peut être sélectionnée ci-dessous et est indiquée dans l'aperçu à droite. Les données ne contiennent aucune information personnelle et nous aident à définir les priorités pour le développement ultérieur d'ioBroker dans l'intérêt de tous les utilisateurs et pour pouvoir corriger rapidement les erreurs qui se produisent. <br> Merci beaucoup d'avoir soutenu notre travail acharné en acceptant la transmission des données statistiques!", "it": "Aiutaci a rendere ioBroker ancora migliore! <br> Consentire la trasmissione di statistiche anonime sull'utilizzo e rapporti sugli arresti anomali. L'ambito esatto dei dati inviati a iobroker.org durante l'aggiornamento dell'elenco degli adattatori può essere selezionato di seguito e viene mostrato nella panoramica a destra. I dati non contengono alcuna informazione personale e ci aiutano a stabilire le priorità per l'ulteriore sviluppo di ioBroker nell'interesse di tutti gli utenti e per poter correggere rapidamente gli errori che si verificano. <br> Mille grazie per aver supportato il nostro duro lavoro accettando la trasmissione dei dati statistici!\"", "es": "¡Ayúdanos a mejorar ioBroker! <br> Permitir la transmisión de estadísticas de uso anónimas e informes de fallos. El alcance exacto de los datos enviados a iobroker.org al actualizar la lista de adaptadores se puede seleccionar a continuación y se muestra en la descripción general a la derecha. Los datos no contienen ninguna información personal y nos ayudan a establecer las prioridades para el desarrollo posterior de ioBroker en interés de todos los usuarios y para poder corregir los errores que ocurren rápidamente. <br> ¡Muchas gracias por apoyar nuestro arduo trabajo al aceptar la transmisión de los datos estadísticos!", "pl": "Pomóż nam ulepszyć ioBroker! <br> Zezwól na przesyłanie anonimowych statystyk użytkowania i raportów o awariach. Dokładny zakres danych wysyłanych do iobroker.org podczas aktualizacji listy adapterów można wybrać poniżej i pokazano w przeglądzie po prawej stronie. Dane nie zawierają żadnych danych osobowych i pomagają nam ustalić priorytety dla dalszego rozwoju ioBroker w interesie wszystkich użytkowników i pozwalają nam szybko naprawić występujące błędy. <br> Ogromne dzięki za wsparcie naszej ciężkiej pracy, wyrażając zgodę na przesyłanie danych statystycznych!", "zh-cn": "帮助我们使ioBroker变得更好！ <br>允许传输匿名使用情况统计信息和崩溃报告。可以在下面选择更新适配器列表时发送到iobroker.org的数据的确切范围，并在右侧的概述中显示。数据不包含任何个人信息，有助于我们为所有用户的利益设定ioBroker进一步开发的优先级，并能够迅速纠正发生的错误。 <br>非常感谢您同意传输统计数据来支持我们的辛勤工作！"},
