    "letsnecrypt_help": {                            "en": "This are settings for Let's Encrypt account. To get the free certificates for your domain. You can read more <a href=\"https://letsencrypt.org/\" target=\"_blank\">here</a>.", "de": "Einstellungen für das Let's Encrypt Konto. Um ein gratis Zertifikat für Ihre Domain zu erhalten, folgen sie den Anweisungen <a href=\"https://letsencrypt.org/\" target=\"_blank\">hier</a>.", "ru": "Настройки для Let's Encrypt аккаунта. Это позволит получить бесплатные сертификаты для вашего домена. О Let's Encrypt можно почитать <a href=\"https://letsencrypt.org/\" target=\"_blank\">здесь</a>.", "pt": "Configurações para a conta Let's Encrypt. Para obter um certificado gratuito para o seu domínio, siga as instruções <a href=\"https://letsencrypt.org/\" target=\"_blank\">aqui</a>.", "nl": "Instellingen voor het Let's Encrypt-account. Als u een gratis certificaat voor uw domein wilt ontvangen, volgt u <a href=\"https://letsencrypt.org/\" target=\"_blank\">deze instructie</a>.", "fr": "Paramètres du compte Let's Encrypt. Pour obtenir un certificat gratuit pour votre domaine, suivez les instructions <a href=\"https://letsencrypt.org/\" target=\"_blank\">ici</a>.", "it": "Queste sono le impostazioni per l'account Let's Encrypt. Per ottenere i certificati gratuiti per il tuo dominio. Puoi leggere più <a href=\"https://letsencrypt.org/\" target=\"_blank\"> qui </a>.", "es": "Configuraciones para la cuenta Let's Encrypt. Para obtener un certificado gratuito para su dominio, siga las instrucciones <a href=\"https://letsencrypt.org/\" target=\"_blank\">aquí</a>.", "pl": "To są ustawienia dla konta Let's Encrypt. Aby uzyskać bezpłatne certyfikaty dla swojej domeny. Możesz przeczytać więcej <a href=\"https://letsencrypt.org/\" target=\"_blank\"> tutaj </a>.", "zh-cn": "这是Let's Encrypt帐户的设置。获取域名的免费证书。您可以在<a href=\"https://letsencrypt.org/\" target=\"_blank\">此处</a>阅读更多信息。"},
