    "letsnecrypt_help_email": {                      "en": "Please use your email address. It will be used for your account.", "de": "Bitte immer nur eine Email-Adresse benutzen.",    "ru": "Почтовый адрес используется для создания аккаунта.", "pt": "Use sempre apenas um endereço de e-mail.",        "nl": "Gebruik altijd slechts één e-mailadres.",         "fr": "Veuillez toujours utiliser une seule adresse e-mail.", "it": "Si prega di utilizzare il tuo indirizzo email. Sarà usato per il tuo account.", "es": "Utilice siempre sólo una e-mail.",                "pl": "Użyj swojego adresu e-mail. Będzie on używany na Twoim koncie.", "zh-cn": "请使用您的电子邮件地址。它将用于您的帐户。"},
    "letsnecrypt_help_path": {                       "en": "Directory name where the certificates will be stored. This is always relative to configuration directory", "de": "Verzeichnis, in dem die Zertifikate gespeichert werden. Der Pfad ist relativ zum Konfigurationsverzeichnis", "ru": "Путь к папке, где будут хранится сертификаты. Относительно папки с настройками", "pt": "Diretório onde os certificados são gravados. O caminho é relativo ao diretório de configuração", "nl": "Locatie waar de certificaten worden opgeslagen. Pad is relatief ten opzichte van de configuratiemap", "fr": "Répertoire où les certificats sont stockés. Le chemin est relatif au répertoire de configuration", "it": "Nome della directory in cui verranno archiviati i certificati. Questo è sempre relativo alla directory di configurazione", "es": "Directorio donde se escriben los certificados. La ruta de acceso se refiere al directorio de configuración", "pl": "Nazwa katalogu, w którym będą przechowywane certyfikaty. Jest to zawsze zależne od katalogu konfiguracji", "zh-cn": "将存储证书的目录名称。这始终与配置目录相关"},
